import { ReactTestRenderer, act } from 'react-test-renderer';
import { performance } from 'perf_hooks';

export interface PerformanceMetrics {
  renderTime: number;
  memoryUsage: number;
  componentCount: number;
  updateTime?: number;
  mountTime?: number;
  unmountTime?: number;
}

export interface PerformanceTestOptions {
  iterations?: number;
  warmupIterations?: number;
  timeout?: number;
  measureMemory?: boolean;
  measureUpdates?: boolean;
}

export class PerformanceTestUtils {
  private static defaultOptions: PerformanceTestOptions = {
    iterations: 10,
    warmupIterations: 3,
    timeout: 5000,
    measureMemory: true,
    measureUpdates: true,
  };

  /**
   * ÊµãÈáèÁªÑ‰ª∂Ê∏≤ÊüìÊÄßËÉΩ
   */
  static async measureRenderPerformance(
    renderComponent: () => ReactTestRenderer,
    options: PerformanceTestOptions = {}
  ): Promise<PerformanceMetrics> {
    const opts = { ...this.defaultOptions, ...options };
    const metrics: PerformanceMetrics[] = [];

    // È¢ÑÁÉ≠
    for (let i = 0; i < opts.warmupIterations!; i++) {
      const renderer = renderComponent();
      renderer.unmount();
    }

    // Ê≠£ÂºèÊµãËØï
    for (let i = 0; i < opts.iterations!; i++) {
      const metric = await this.measureSingleRender(renderComponent, opts);
      metrics.push(metric);
    }

    return this.calculateAverageMetrics(metrics);
  }

  /**
   * ÊµãÈáèÂçïÊ¨°Ê∏≤ÊüìÊÄßËÉΩ
   */
  private static async measureSingleRender(
    renderComponent: () => ReactTestRenderer,
    options: PerformanceTestOptions
  ): Promise<PerformanceMetrics> {
    const startMemory = options.measureMemory ? this.getMemoryUsage() : 0;
    
    // ÊµãÈáèÊåÇËΩΩÊó∂Èó¥
    const mountStart = performance.now();
    const renderer = renderComponent();
    const mountEnd = performance.now();
    const mountTime = mountEnd - mountStart;

    // ÊµãÈáèÊ∏≤ÊüìÊó∂Èó¥
    const renderStart = performance.now();
    await act(async () => {
      // Á≠âÂæÖÊ∏≤ÊüìÂÆåÊàê
      await new Promise(resolve => setTimeout(resolve, 0));
    });
    const renderEnd = performance.now();
    const renderTime = renderEnd - renderStart;

    // ËÆ°ÁÆóÁªÑ‰ª∂Êï∞Èáè
    const componentCount = this.countComponents(renderer);

    // ÊµãÈáèÊõ¥Êñ∞Êó∂Èó¥ÔºàÂ¶ÇÊûúÂêØÁî®Ôºâ
    let updateTime: number | undefined;
    if (options.measureUpdates) {
      const updateStart = performance.now();
      await act(async () => {
        renderer.update(renderer.getInstance());
      });
      const updateEnd = performance.now();
      updateTime = updateEnd - updateStart;
    }

    // ÊµãÈáèÂç∏ËΩΩÊó∂Èó¥
    const unmountStart = performance.now();
    renderer.unmount();
    const unmountEnd = performance.now();
    const unmountTime = unmountEnd - unmountStart;

    const endMemory = options.measureMemory ? this.getMemoryUsage() : 0;
    const memoryUsage = endMemory - startMemory;

    return {
      renderTime,
      memoryUsage,
      componentCount,
      updateTime,
      mountTime,
      unmountTime,
    };
  }

  /**
   * ËÆ°ÁÆóÂπ≥ÂùáÊÄßËÉΩÊåáÊ†á
   */
  private static calculateAverageMetrics(metrics: PerformanceMetrics[]): PerformanceMetrics {
    const count = metrics.length;
    
    return {
      renderTime: metrics.reduce((sum, m) => sum + m.renderTime, 0) / count,
      memoryUsage: metrics.reduce((sum, m) => sum + m.memoryUsage, 0) / count,
      componentCount: metrics.reduce((sum, m) => sum + m.componentCount, 0) / count,
      updateTime: metrics[0].updateTime !== undefined 
        ? metrics.reduce((sum, m) => sum + (m.updateTime || 0), 0) / count 
        : undefined,
      mountTime: metrics.reduce((sum, m) => sum + (m.mountTime || 0), 0) / count,
      unmountTime: metrics.reduce((sum, m) => sum + (m.unmountTime || 0), 0) / count,
    };
  }

  /**
   * Ëé∑ÂèñÂÜÖÂ≠ò‰ΩøÁî®ÊÉÖÂÜµ
   */
  private static getMemoryUsage(): number {
    if (typeof process !== 'undefined' && process.memoryUsage) {
      return process.memoryUsage().heapUsed;
    }
    return 0;
  }

  /**
   * ËÆ°ÁÆóÁªÑ‰ª∂Êï∞Èáè
   */
  private static countComponents(renderer: ReactTestRenderer): number {
    try {
      const instance = renderer.root;
      return this.countComponentsRecursive(instance);
    } catch {
      return 0;
    }
  }

  /**
   * ÈÄíÂΩíËÆ°ÁÆóÁªÑ‰ª∂Êï∞Èáè
   */
  private static countComponentsRecursive(instance: any): number {
    let count = 1;
    
    try {
      if (instance.children) {
        for (const child of instance.children) {
          count += this.countComponentsRecursive(child);
        }
      }
    } catch {
      // ÂøΩÁï•ÈîôËØØÔºåÁªßÁª≠ËÆ°ÁÆó
    }
    
    return count;
  }

  /**
   * ÊÄßËÉΩÂü∫ÂáÜÊµãËØï
   */
  static async benchmarkComponent(
    name: string,
    renderComponent: () => ReactTestRenderer,
    options: PerformanceTestOptions = {}
  ): Promise<void> {
    console.log(`\nüöÄ ÂºÄÂßãÊÄßËÉΩÊµãËØï: ${name}`);
    
    const startTime = performance.now();
    const metrics = await this.measureRenderPerformance(renderComponent, options);
    const totalTime = performance.now() - startTime;

    console.log(`üìä ÊÄßËÉΩÊµãËØïÁªìÊûú - ${name}:`);
    console.log(`  ‚è±Ô∏è  Ê∏≤ÊüìÊó∂Èó¥: ${metrics.renderTime.toFixed(2)}ms`);
    console.log(`  üß† ÂÜÖÂ≠ò‰ΩøÁî®: ${(metrics.memoryUsage / 1024 / 1024).toFixed(2)}MB`);
    console.log(`  üî¢ ÁªÑ‰ª∂Êï∞Èáè: ${Math.round(metrics.componentCount)}`);
    
    if (metrics.mountTime) {
      console.log(`  üì¶ ÊåÇËΩΩÊó∂Èó¥: ${metrics.mountTime.toFixed(2)}ms`);
    }
    
    if (metrics.updateTime) {
      console.log(`  üîÑ Êõ¥Êñ∞Êó∂Èó¥: ${metrics.updateTime.toFixed(2)}ms`);
    }
    
    if (metrics.unmountTime) {
      console.log(`  üóëÔ∏è  Âç∏ËΩΩÊó∂Èó¥: ${metrics.unmountTime.toFixed(2)}ms`);
    }
    
    console.log(`  ‚è∞ ÊÄªÊµãËØïÊó∂Èó¥: ${totalTime.toFixed(2)}ms`);
    
    // ÊÄßËÉΩË≠¶Âëä
    if (metrics.renderTime > 100) {
      console.warn(`‚ö†Ô∏è  Ë≠¶Âëä: ${name} Ê∏≤ÊüìÊó∂Èó¥ËøáÈïø (${metrics.renderTime.toFixed(2)}ms)`);
    }
    
    if (metrics.memoryUsage > 50 * 1024 * 1024) { // 50MB
      console.warn(`‚ö†Ô∏è  Ë≠¶Âëä: ${name} ÂÜÖÂ≠ò‰ΩøÁî®ËøáÂ§ö (${(metrics.memoryUsage / 1024 / 1024).toFixed(2)}MB)`);
    }
  }

  /**
   * ÊØîËæÉ‰∏§‰∏™ÁªÑ‰ª∂ÁöÑÊÄßËÉΩ
   */
  static async comparePerformance(
    name1: string,
    renderComponent1: () => ReactTestRenderer,
    name2: string,
    renderComponent2: () => ReactTestRenderer,
    options: PerformanceTestOptions = {}
  ): Promise<void> {
    console.log(`\nüîÑ ÂºÄÂßãÊÄßËÉΩÂØπÊØî: ${name1} vs ${name2}`);
    
    const metrics1 = await this.measureRenderPerformance(renderComponent1, options);
    const metrics2 = await this.measureRenderPerformance(renderComponent2, options);
    
    console.log(`üìä ÊÄßËÉΩÂØπÊØîÁªìÊûú:`);
    console.log(`  ${name1}:`);
    console.log(`    ‚è±Ô∏è  Ê∏≤ÊüìÊó∂Èó¥: ${metrics1.renderTime.toFixed(2)}ms`);
    console.log(`    üß† ÂÜÖÂ≠ò‰ΩøÁî®: ${(metrics1.memoryUsage / 1024 / 1024).toFixed(2)}MB`);
    
    console.log(`  ${name2}:`);
    console.log(`    ‚è±Ô∏è  Ê∏≤ÊüìÊó∂Èó¥: ${metrics2.renderTime.toFixed(2)}ms`);
    console.log(`    üß† ÂÜÖÂ≠ò‰ΩøÁî®: ${(metrics2.memoryUsage / 1024 / 1024).toFixed(2)}MB`);
    
    // ËÆ°ÁÆóÊÄßËÉΩÂ∑ÆÂºÇ
    const renderTimeDiff = ((metrics2.renderTime - metrics1.renderTime) / metrics1.renderTime) * 100;
    const memoryDiff = ((metrics2.memoryUsage - metrics1.memoryUsage) / metrics1.memoryUsage) * 100;
    
    console.log(`  üìà ÊÄßËÉΩÂ∑ÆÂºÇ:`);
    console.log(`    ‚è±Ô∏è  Ê∏≤ÊüìÊó∂Èó¥: ${renderTimeDiff > 0 ? '+' : ''}${renderTimeDiff.toFixed(1)}%`);
    console.log(`    üß† ÂÜÖÂ≠ò‰ΩøÁî®: ${memoryDiff > 0 ? '+' : ''}${memoryDiff.toFixed(1)}%`);
    
    // ÊÄßËÉΩÂª∫ËÆÆ
    if (Math.abs(renderTimeDiff) < 5) {
      console.log(`‚úÖ Ê∏≤ÊüìÊÄßËÉΩÁõ∏Ëøë`);
    } else if (renderTimeDiff < 0) {
      console.log(`üéâ ${name2} Ê∏≤ÊüìÊÄßËÉΩÊõ¥Â•Ω`);
    } else {
      console.log(`üéâ ${name1} Ê∏≤ÊüìÊÄßËÉΩÊõ¥Â•Ω`);
    }
  }

  /**
   * ÂÜÖÂ≠òÊ≥ÑÊºèÊ£ÄÊµã
   */
  static async detectMemoryLeaks(
    name: string,
    renderComponent: () => ReactTestRenderer,
    iterations: number = 100
  ): Promise<boolean> {
    console.log(`\nüîç ÂºÄÂßãÂÜÖÂ≠òÊ≥ÑÊºèÊ£ÄÊµã: ${name}`);
    
    const initialMemory = this.getMemoryUsage();
    const memorySnapshots: number[] = [];
    
    for (let i = 0; i < iterations; i++) {
      const renderer = renderComponent();
      
      // Ê®°Êãü‰∏Ä‰∫õÊìç‰Ωú
      await act(async () => {
        renderer.update(renderer.getInstance());
      });
      
      renderer.unmount();
      
      // ÊØè10Ê¨°Ëø≠‰ª£ËÆ∞ÂΩïÂÜÖÂ≠ò‰ΩøÁî®
      if (i % 10 === 0) {
        // Âº∫Âà∂ÂûÉÂúæÂõûÊî∂ÔºàÂ¶ÇÊûúÂèØÁî®Ôºâ
        if (global.gc) {
          global.gc();
        }
        
        const currentMemory = this.getMemoryUsage();
        memorySnapshots.push(currentMemory);
      }
    }
    
    // ÂàÜÊûêÂÜÖÂ≠òË∂ãÂäø
    const memoryGrowth = memorySnapshots[memorySnapshots.length - 1] - memorySnapshots[0];
    const growthPercentage = (memoryGrowth / initialMemory) * 100;
    
    console.log(`üìä ÂÜÖÂ≠òÊ≥ÑÊºèÊ£ÄÊµãÁªìÊûú - ${name}:`);
    console.log(`  üß† ÂàùÂßãÂÜÖÂ≠ò: ${(initialMemory / 1024 / 1024).toFixed(2)}MB`);
    console.log(`  üìà ÂÜÖÂ≠òÂ¢ûÈïø: ${(memoryGrowth / 1024 / 1024).toFixed(2)}MB (${growthPercentage.toFixed(1)}%)`);
    
    const hasMemoryLeak = growthPercentage > 20; // 20%ÈòàÂÄº
    
    if (hasMemoryLeak) {
      console.warn(`‚ö†Ô∏è  Ê£ÄÊµãÂà∞ÊΩúÂú®ÂÜÖÂ≠òÊ≥ÑÊºè!`);
    } else {
      console.log(`‚úÖ Êú™Ê£ÄÊµãÂà∞ÊòéÊòæÂÜÖÂ≠òÊ≥ÑÊºè`);
    }
    
    return hasMemoryLeak;
  }
}

/**
 * ÊÄßËÉΩÊµãËØïË£ÖÈ•∞Âô®
 */
export function performanceTest(options: PerformanceTestOptions = {}) {
  return function (target: any, propertyName: string, descriptor: PropertyDescriptor) {
    const method = descriptor.value;
    
    descriptor.value = async function (...args: any[]) {
      const startTime = performance.now();
      const result = await method.apply(this, args);
      const endTime = performance.now();
      
      console.log(`‚è±Ô∏è  ${propertyName} ÊâßË°åÊó∂Èó¥: ${(endTime - startTime).toFixed(2)}ms`);
      
      return result;
    };
    
    return descriptor;
  };
}

/**
 * ÊÄßËÉΩÊñ≠Ë®Ä
 */
export class PerformanceAssertions {
  static expectRenderTimeBelow(metrics: PerformanceMetrics, maxTime: number): void {
    if (metrics.renderTime > maxTime) {
      throw new Error(`Ê∏≤ÊüìÊó∂Èó¥ ${metrics.renderTime.toFixed(2)}ms Ë∂ÖËøáÈ¢ÑÊúü ${maxTime}ms`);
    }
  }
  
  static expectMemoryUsageBelow(metrics: PerformanceMetrics, maxMemory: number): void {
    if (metrics.memoryUsage > maxMemory) {
      throw new Error(`ÂÜÖÂ≠ò‰ΩøÁî® ${(metrics.memoryUsage / 1024 / 1024).toFixed(2)}MB Ë∂ÖËøáÈ¢ÑÊúü ${(maxMemory / 1024 / 1024).toFixed(2)}MB`);
    }
  }
  
  static expectComponentCountBelow(metrics: PerformanceMetrics, maxCount: number): void {
    if (metrics.componentCount > maxCount) {
      throw new Error(`ÁªÑ‰ª∂Êï∞Èáè ${Math.round(metrics.componentCount)} Ë∂ÖËøáÈ¢ÑÊúü ${maxCount}`);
    }
  }
} 